/**
 * Returns the intended color value for a partially specified color value.
 * Can request 8 or 6 digit hex rgb return values.  Note that 'transparent' is
 * always returned unconverted as it is a valid css value and requested to stay
 * untouched for ux reasons.  Finds first valid hex substring and applies
 * heuristics.
 *
 * Returns '' if the value is not valid.
 * @param value
 * @returns
 */
export const getExpandedColorValue = (
  value?: string | ColorValue,
  hasEightDigitHex = false
): ColorValue => {
  if (!value) {
    return '' as ColorValue;
  }
  if (isTransparent(value)) {
    return value as ColorValue;
  }
  // Return eight digit hex results
  if (hasEightDigitHex) {
    // First check if this is a complete value and return that
    if (isValidColorValue(value)) {
      return getEightDigitHex(getColorValue(value));
    }
    // Now implement desired behavior for partially specified color values

    // strip leading hash
    const nohash = value[0] === '#' ? value.substring(1) : value;
    // Ux request is to strip all non-hex characters first , upper-case, and then apply heuristics
    const val = nohash?.match(/[0-9A-Fa-f]+/)?.[0].toUpperCase() ?? '';

    // A -> #AAAAAAFF
    if (/^([0-9A-F]{1})$/.test(val)) {
      return ('#' + val + val + val + val + val + val + 'FF') as ColorValue;
    }

    // AB -> #ABABABFF
    if (/^([0-9A-F]{2})$/.test(val)) {
      return ('#' + val[0] + val[1] + val[0] + val[1] + val[0] + val[1] + 'FF') as ColorValue;
    }

    // ABC -> #AABBCCFF
    if (/^([0-9A-F]{3})$/.test(val)) {
      return ('#' + val[0] + val[0] + val[1] + val[1] + val[2] + val[2] + 'FF') as ColorValue;
    }

    // ABCF -> #AABBCCFF
    if (/^([0-9A-F]{4})$/.test(val)) {
      return ('#' +
        val[0] +
        val[0] +
        val[1] +
        val[1] +
        val[2] +
        val[2] +
        val[3] +
        val[3]) as ColorValue;
    }

    // ABCDEF -> #ABCDEFFF
    if (/^([0-9A-F]{6})$/.test(val)) {
      return ('#' + val[0] + val[1] + val[2] + val[3] + val[4] + val[5] + 'FF') as ColorValue;
    }

    // ABCDEFFF -> #ABCDEFFF
    if (/^([0-9A-F]{8})$/.test(val)) {
      return ('#' +
        val[0] +
        val[1] +
        val[2] +
        val[3] +
        val[4] +
        val[5] +
        val[6] +
        val[7]) as ColorValue;
    }
  } else {
    // 6 digit hex path return
    // First check if this is a complete value and return that
    if (isValidColorValue(value)) {
      return getSixDigitHex(getColorValue(value));
    }

    // strip leading hash
    const nohash = value[0] === '#' ? value.substring(1) : value;
    // Ux request is to strip all non-hex characters first , upper-case, and then apply heuristics
    const val = nohash?.match(/[0-9A-Fa-f]+/)?.[0].toUpperCase() ?? '';

    // A -> #AAAAAA
    if (/^([0-9A-F]{1})$/.test(val)) {
      return ('#' + val + val + val + val + val + val) as ColorValue;
    }

    // AB -> #ABABAB
    if (/^([0-9A-F]{2})$/.test(val)) {
      return ('#' + val[0] + val[1] + val[0] + val[1] + val[0] + val[1]) as ColorValue;
    }

    // ABC -> #AABBCC
    if (/^([0-9A-F]{3})$/.test(val)) {
      return ('#' + val[0] + val[0] + val[1] + val[1] + val[2] + val[2]) as ColorValue;
    }

    // ABCDEF -> #ABCDEF
    if (/^([0-9A-F]{6})$/.test(val)) {
      return ('#' + val[0] + val[1] + val[2] + val[3] + val[4] + val[5]) as ColorValue;
    }
  }

  return '' as ColorValue;
};
